import { groupBy, map, upperFirst } from 'lodash'
import { FormattedMessage } from 'react-intl'
import { Card, Col, Row, Typography } from 'antd'

import { SKILL_TYPES } from '../../utils/constants'
import Progress from '../../components/Progress'
import messages from './messages'

const { Meta } = Card
const { Title } = Typography

const Skills = ({ data: propsData }) => {
  const data = groupBy(propsData, ({ type }) => type || SKILL_TYPES.FRONTEND)

  return (
    <Card bordered={false} bodyStyle={{ backgroundColor: '#e8d5b6' }}>
      <Meta
        title={(
          <Title level={2}>
            <FormattedMessage {...messages.title} />
          </Title>
        )}
        description="I Like To Craft Beautiful And Scalable Web Products"
      />
      <Row justify="space-between">
        {Object.entries(data).map(([type, skills]) => (
          <Col key={type} span={11}>
            <Card
              title={upperFirst(type)}
              bordered={false}
              headStyle={{ backgroundColor: '#e8d5b6', textAlign: 'center' }}
              bodyStyle={{ backgroundColor: '#e8d5b6' }}
            >
              {map(skills, ({ type, name, color, ...restProps }) => (
                <Progress
                  key={`${type}-${name}`}
                  label={name}
                  strokeColor={color}
                  {...restProps}
                />
              ))}
            </Card>
          </Col>
        ))}
      </Row>
    </Card>
  )
}

export default Skills
