import { kebabCase } from 'lodash'
import { FormattedMessage } from 'react-intl'
import { Card, Timeline, Typography } from 'antd'

import TimelineLabel from './TimelineLabel'
import messages from './messages'

const { Meta } = Card
const { Title } = Typography

const Experience = ({ data }) => {
  const title = (
    <Title level={2}>
      <FormattedMessage {...messages.title} />
    </Title>
  )

  return (
    <Card bordered={false} bodyStyle={{ backgroundColor: '#e8d5b6' }}>
      <Meta
        title={title}
        description="This is the description. This is the description. This is the description. This is the description. This is the description. This is the description. This is the description. This is the description. This is the description. This is the description. This is the description. This is the description. This is the description. This is the description. This is the description. This is the description. This is the description. This is the description. This is the description. This is the description. This is the description. This is the description. This is the description. This is the description. This is the description"
      />
      <Timeline mode="alternate" pending={<FormattedMessage {...messages.present} />}>
        {data.map(({ company, ...restProps }) => (
          <TimelineLabel
            key={kebabCase(company)}
            {...restProps}
          />
        ))}
      </Timeline>
    </Card>
  )
}

export default Experience
