import { Card, Row, Typography } from 'antd'

import { StyledTimelineItem, NoMarginTitle, TimelineDot } from './Styled'

const { Meta } = Card
const { Text } = Typography

const TimelineLabel = ({ title: propsTitle, icon, from, to, description, ...restProps }) => {
  const title = (
    <Row justify="space-between" align="middle">
      <NoMarginTitle level={3}>
        {propsTitle}
      </NoMarginTitle>
      <Text type="secondary">
        {from} - {to}
      </Text>
    </Row>
  )

  return (
    <StyledTimelineItem dot={TimelineDot} {...restProps}>
      <Card bordered={false} bodyStyle={{ backgroundColor: '#f2f3f7' }}>
        <Meta title={title} description={description} />
      </Card>
    </StyledTimelineItem>
  )
}

export default TimelineLabel
